import React, { useState } from 'react';
import { Layout, Menu, Button, Badge, Popover  } from 'antd';
import Logo from "../../Assets/images/logo.png";
import Dashboard from "../../Assets/images/Dashboard.png";
import Dispatch from "../../Assets/images/Dispatch.png";
import Customers from "../../Assets/images/Customers.png";
import Sales from "../../Assets/images/Sales.png";
import Finance from "../../Assets/images/Finance.png";
import Apps from "../../Assets/images/Apps.png";
import Chat from "../../Assets/images/Chat.png";
import Settings from "../../Assets/images/Settings.png";
import Plussign from "../../Assets/images/plussign.png";
import Profile from "../../Assets/images/profile.png";
import DownArrow from "../../Assets/images/down-arrow.png";
import Hamburgermenu from "../../Assets/images/hamburgermenu.png";
import { MdDonutSmall } from "react-icons/md";
import { ImRadioChecked } from "react-icons/im";
import { MdMonetizationOn, MdAdjust, MdAccountCircle, MdCheckCircle, MdChat, MdSettings, MdApps, MdAdd } from "react-icons/md";
import {Link} from "react-router-dom";














import "./Header.css";

// const { Header} = Layout;



const content = (
  <div className="submenu">
    <p>Content</p>
    <p><a href="#">Logout</a></p>
  </div>
);






const Header = () => {
  const [toggleMenu, setToggleMenu] = useState(false);
    return (
      <>
      <Layout className="header" defaultSelectedKeys={['3']}>
          <div className="headerfield">
          <div className="headerleft_field">          
            <div className="logo">
                <img src={Logo} alt="logo"/>
            </div>
            <div onClick={()=>setToggleMenu(!toggleMenu)} className="hamburgermenu" >
                <img src={Hamburgermenu} alt="hamburgermenu"/>
            </div>
            <div className="headerpageHeading">
                  <h4>Add customer</h4>
            </div>
            <Menu mode="horizontal"  className="tabletmenu">
                <Menu.Item key="1">
                  <Link to="dashboard">
                    <div className="menulogo">
                      <MdDonutSmall style={{color:'#fff', fontSize: '24px'}}/>
                    </div>              
                    Dashboard
                  </Link>
                </Menu.Item>
                <Menu.Item key="2">
                  <Link to="dispatch">
                    <div className="menulogo">
                      <MdCheckCircle style={{color:'#fff', fontSize: '24px'}}/>
                    </div> 
                    Dispatch
                  </Link>
                </Menu.Item>
                <Menu.Item key="3">
                  <Link to="customers">
                      <div className="menulogo">
                        <MdAccountCircle style={{color:'#fff', fontSize: '24px'}}/>
                      </div> 
                      Customers
                    </Link>
                </Menu.Item>
                <Menu.Item key="4">
                  <Link to="sales">
                      <div className="menulogo">
                        <ImRadioChecked style={{color:'#fff', fontSize: '24px'}}/>
                      </div> 
                      Sales
                  </Link>
                </Menu.Item>
                <Menu.Item key="5">
                  <Link to="finance">
                      <div className="menulogo">
                        <MdMonetizationOn style={{color:'#fff', fontSize: '24px'}}/>
                      </div> 
                      Finance
                    </Link>
                </Menu.Item>
          </Menu>
          </div>
          <div  className="headerright_field">
              <Menu mode="horizontal">
                  <Menu.Item key="6" className="appmenu">
                      <Link to="apps">
                          <div className="menulogo">
                            <MdApps style={{color:'#fff', fontSize: '24px'}}/>
                          </div>  
                          Apps
                      </Link>
                  </Menu.Item>
                  <Menu.Item key="7" className="chatmenu">
                      <Link to="chat">
                        <div className="menulogo">
                        <Badge count={2}>
                            <a href="#" className="head-example" />
                        </Badge>
                          <MdChat style={{color:'#fff', fontSize: '24px'}}/>
                        </div>  
                          Chat
                      </Link>
                  </Menu.Item>
                  <Menu.Item key="8" className="settingmenu">
                      <Link to="/">
                        <div className="menulogo">
                          <MdSettings style={{color:'#fff', fontSize: '24px'}}/>
                        </div>  
                        Settings
                      </Link>
                  </Menu.Item>
                  <Menu.Item key="New" className="newbtnmenu">
                      <Button className="addbutton">
                        <span className="nesplussign">
                            {/* <img src={Plussign} alt="plussign"/>   */}
                            <MdAdd style={{color:'#fff', fontSize: '24px'}}/>
                        </span> 
                        New</Button>
                  </Menu.Item>
                  <Menu.Item key="profile" className="profilelist">
                    <Popover content={content}>
                        <div className="profilefield">
                          <span className="profilepic">
                                <img src={Profile} alt="profile"/>  
                            </span> 
                            <span className="">
                                <img src={DownArrow} alt="down-arrow"/>  
                            </span> 
                        </div>
                    </Popover>
                  </Menu.Item>
                </Menu>
          </div>
        </div>
      </Layout>
      <div className={toggleMenu ? "responsive_menu open" : "responsive_menu"}>
          <Menu mode="horizontal" defaultSelectedKeys={['1']} className="tabletmenu">
            <Menu.Item key="1">
              <div className="menulogo">
                <img src={Dashboard} alt="Dashboard"/>
              </div>              
              Dashboard
            </Menu.Item>
            <Menu.Item key="2">
              <div className="menulogo">
                <img src={Dispatch} alt="Dispatch"/>
              </div> 
              Dispatch
            </Menu.Item>
            <Menu.Item key="3">
              <div className="menulogo">
                <img src={Customers} alt="Customers"/>
              </div> 
              Customers
            </Menu.Item>
            <Menu.Item key="4">
              <div className="menulogo">
                <img src={Sales} alt="Sales"/>
              </div> 
              Sales
            </Menu.Item>
            <Menu.Item key="5">
              <div className="menulogo">
                <img src={Finance} alt="Finance"/>
              </div> 
              Finance
            </Menu.Item>
            <Menu.Item key="Apps" className="appmenu">
              <div className="menulogo">
                <img src={Apps} alt="Apps"/>
              </div>  
              Apps
            </Menu.Item>
              <Menu.Item key="Chat" className="chatmenu">
                <div className="menulogo">
                <Badge count={2}>
                    <a href="#" className="head-example" />
                </Badge>
                  <img src={Chat} alt="Chat"/>
                </div>  
                Chat
              </Menu.Item>
              <Menu.Item key="Settings" className="settingmenu">
                <div className="menulogo">
                  <img src={Settings} alt="Settings"/>
                </div>  
                Settings
            </Menu.Item>
          </Menu>
      </div>
      </>
    )
}
export default Header;