import React, {useState } from 'react'
import { Row, Col, Tabs,Typography, Drawer,Modal, Input } from 'antd';
import CustomerDetails from '../CustomerDetails/CustomerDetails';
import PropertyDetails from '../PropertyDetails/PropertyDetails';
import AdditionalPropertyDetails from "../AdditionalPropertyDetails/AdditionalPropertyDetails";
import CommunicationsSettings from '../CommunicationsSettings/CommunicationsSettings';
import AdditionalCustomerDetails from '../AdditionalCustomerDetails/AdditionalCustomerDetails';
// import Header from '../Header/Header';
// import Footer from '../Footer/Footer';
import { MdBusiness,MdStars, MdGrade, MdLocationOn,MdLanguage, MdQueryBuilder,MdEmail,MdClose, MdSupervisorAccount,MdPhoneIphone,MdPayment, MdEvent, MdMonetizationOn,MdForum, MdAccountBox } from "react-icons/md";
import CompanyProfile from "../../Assets/images/company_profile.svg";
import User from "../../Assets/images/user.svg";
import Jobapplicatoin from "../../Assets/images/jobapplicatoin.svg";
import { BiListCheck } from "react-icons/bi";

import Blackmenubtn from "../../Assets/images/black-hamburger-menu.svg";

import "./Settings.css";
import BusinesModal from '../Modal/BusinessModal/BusinessModal';
import BusinessServiceType from "../Modal/BusinessServiceType/BusinessServiceType";
import ContactEmail from "../Modal/ContactEmail/ContactEmail";
import WebsiteAddress from "../Modal/WebsiteAddress/WebsiteAddress";
import BusinessAddress from "../Modal/BusinessAddress/BusinessAddress";
import PhoneNumberModal from "../Modal/PhoneNumberModal/PhoneNumberModal";
import CountryModal from "../Modal/CountryModal/CountryModal";
import LanguageModal from "../Modal/LanguageModal/LanguageModal";
import DateFormat from "../Modal/DateFormat/DateFormat";
import TimeFormat from "../Modal/TimeFormat/TimeFormat";
import TimeZoneModal from '../Modal/TimeZoneModal/TimeZoneModal';
import BusinessHours from '../Modal/BusinessHours/BusinessHours';


const Settings = () => {
    const { TabPane } = Tabs;
    const [editableStr, setEditableStr] = useState("Cleaning Pro"); 
    const [cleaningservice, setCleaningservice] = useState("Cleaning services "); 
    const [address, setAddress] = useState({address: "646 Mill Street\nSeaforth, ON N0K 1W0", state: "", city: "", zip: ""});
    const [watch, setWatch] = useState("Sunday      Closed\nMonday  08:00 - 17:00\nTuesday  08:00 - 17:00\nWednesday  08:00 - 17:00ed\nThursday  08:00 - 17:00\nFriday  08:00 - 17:00\nSaturday  08:00 - 17:00\nSaturday  08:00 - 17:00");
    const [phone, setPhone] = useState("+1 (546) 657-99023");
    const [earth, setEarth] = useState("www.cleaningpro.com");
    const [mail, setMail] = useState("contact@cleaningpro.com");   

    const [country, setCountry] = useState("Canada"); 
    const [timezone, setTimezone] = useState("(GMT -05:00) America/Toronto");
    const [dateformat, setDateformat] = useState("31 January, 2021");
    const [timeformat, setTimeformat] = useState("24 Hours");    
    const [language, setLanguage] = useState("English");


    const { Paragraph } = Typography;
    const  createMarkup = (htmlContent) => {
        return {__html: htmlContent};
    }

    const [visible, setVisible] = useState(false);
    const showDrawer = () => {
        setVisible(true);
        
    };
    const onClose = () => {
        setVisible(false);
    };

    const [businessModal, toggleBusinessModal] = useState(false);
    const [cleaningServiceModal, toggleCleaningServiceModal] = useState(false);
    const [emilModal, toggleEmilModal] = useState(false);
    const [websiteModal, toggleWebsiteModal] = useState(false);
    const [businessaddressModal, toggleBusinessAddressModal] = useState(false);
    const [phonenumberModal, togglePhoneNumberModal] = useState(false);
    const [countryModal, toggleCountryModal] = useState(false);
    const [languageModal, toggleLanguageModal] = useState(false);
    const [dateModal, toggleDateModal] = useState(false);
    const [timeModal, toggleTimeModal] = useState(false);
    const [timezoneModal, toggleTimeZoneModal] = useState(false);
    const [watchModal, toggleWatchModal] = useState(false);
    
    const showModal  = () => {
        toggleBusinessModal(true);
    };

    const showcleaningservcier  = () => {
        toggleCleaningServiceModal(true);
    };

    const showemail  = () => {
        toggleEmilModal(true);
    };

    const showwebsite  = () => {
        toggleWebsiteModal(true);
    };

    const showbusinessaddress  = () => {
        toggleBusinessAddressModal(true);
    };

    const showphonenumber  = () => {
        togglePhoneNumberModal(true);
    };

    const showcountry  = () => {
        toggleCountryModal(true);
    };

    const showlanguage  = () => {
        toggleLanguageModal(true);
    };

    const showdateformat  = () => {
        toggleDateModal(true);
    };    

    const showtimeformat = () => {
        toggleTimeModal(true);
    }; 

    const showtimezone = () => {
        toggleTimeZoneModal(true);
    };

    const showwatch = () => {
        toggleWatchModal(true);
    };


    const handleCancel   = () => {
        toggleBusinessModal(false);
        toggleCleaningServiceModal(false);
        toggleEmilModal(false);
        toggleWebsiteModal(false);
        toggleBusinessAddressModal(false);
        togglePhoneNumberModal(false);
        toggleCountryModal(false);
        toggleLanguageModal(false);
        toggleDateModal(false);
        toggleTimeModal(false);
        toggleTimeZoneModal(false);
        toggleWatchModal(false);

    };

    const confirmBusinessChange  = (value) => {
        setEditableStr(value);
        handleCancel();
    };

    const confirmCleaningChange  = (value) => {
        setCleaningservice(value);
        handleCancel();
    };

    const confirmEmailChange  = (value) => {
        setMail(value);
        handleCancel();
    };

    const confirmWebsiteChange  = (value) => {
        setEarth(value);
        handleCancel();
    };
    
    const confirmBusinessAddressChange  = (address) => {
        setAddress({...address});
        handleCancel();
    };

    const confirmPhoneNumberChange  = (value) => {
        setPhone(value);
        handleCancel();
    };

    const confirmCountryChange  = (value) => {
        setCountry(value);
        handleCancel();
    };

    const confirmLanguageChange  = (value) => {
        setLanguage(value);
        handleCancel();
    };

    const confirmDateChange  = (value) => {
        setDateformat(value);
        handleCancel();
    };

    const confirmTimeFormatChange  = (value) => {
        setTimeformat(value);
        handleCancel();
    };

    const confirmtimezoneChange  = (value) => {
        setTimezone(value);
        handleCancel();
    };

    const confirmwatchChange  = (value) => {
        setWatch(value);
        handleCancel();
    };
    
    const [toggleMenu, setToggleMenu] = useState(false);

    return (
        <div className="customer_form settingpagefield">
            <div className="customer_section">
                <div className="container">
                    <h3 className="pageheading menuheading">Settings
                        <img src={Blackmenubtn} onClick={()=>setToggleMenu(!toggleMenu)}/>
                    </h3>  
                    <div className={toggleMenu ? "tabfield tablistfield open" : "tabfield tablistfield"}>
                        <div className="closebtn" onClick={()=>setToggleMenu(!toggleMenu)}>
                            <MdClose/>
                        </div>
                        <div className="tabbg" onClick={()=>setToggleMenu(!toggleMenu)}></div>                        
                        <Tabs defaultActiveKey="1" >   
                            <TabPane                            
                                tab={
                                    <h3 class="pageheading menuheading responsive_tab_heading">Settings</h3>
                                }
                            >
                            </TabPane>                     
                            <TabPane
                            tab={
                                <span>
                                <MdBusiness/>
                                    Company profile
                                </span>
                            }
                            key="1"
                            >
                            <Row gutter={30}>
                                <Col xs={24} sm={24} md={12} lg={12} xl={12}>
                                    <div className="editfield">
                                        <img src={CompanyProfile} />
                                        <div className="companycontent">
                                            <h3 className="settingsheading">                                                                                                        
                                                <Paragraph 
                                                onClick={showModal}
                                                style={{ whiteSpace: "pre-line" }}
                                                editable={{ onChange: setEditableStr }}>
                                                    {editableStr}
                                                </Paragraph>
                                            </h3>    
                                            <div className="editablefield">                                                
                                                <Paragraph 
                                                onClick={showcleaningservcier}
                                                className="cleaningservice"
                                                style={{ whiteSpace: "pre-line", width: "100%" }}
                                                editable={{ onChange: setCleaningservice }}>
                                                    {cleaningservice}
                                                </Paragraph>
                                            </div> 
                                            <div className="divderborder margintop11"></div>
                                            <div className="addressfield">
                                                <p className="addressmainfield">
                                                    <div className="iconfield">
                                                        <MdLocationOn style={{color: "#1a2f52"}}/>
                                                    </div>                                                    
                                                    <Paragraph 
                                                    onClick={showbusinessaddress}
                                                    style={{ whiteSpace: "pre-line" }}
                                                    editable>
                                                        {address.address +','+ address.city +','+ address.state +','+ address.zip}
                                                    </Paragraph>
                                                </p> 
                                                <p className="addressmainfield margintop23">
                                                    <div className="iconfield">
                                                        <MdQueryBuilder style={{color: "#1a2f52"}}/>
                                                    </div>                                                    
                                                    <Paragraph 
                                                    style={{ whiteSpace: "pre-line"}}                                                     
                                                    onClick={showwatch}
                                                    editable={{ onChange: setWatch }}>
                                                        {/* {watch} */}
                                                        <div className="hoursfield">
                                                            <ul>
                                                                <li>
                                                                    <div className="days">
                                                                    Sunday
                                                                    </div>
                                                                    <div>
                                                                    Closed
                                                                    </div>
                                                                </li>
                                                                <li>
                                                                <div className="days">
                                                                    Monday
                                                                    </div>
                                                                    <div>
                                                                    08:00 - 17:00
                                                                    </div>
                                                                </li>
                                                                <li>
                                                                <div className="days">
                                                                    Tuesday
                                                                    </div>
                                                                    <div>
                                                                    08:00 - 17:00
                                                                    </div>
                                                                </li>
                                                                <li>
                                                                <div className="days">
                                                                    Wednesday
                                                                    </div>
                                                                    <div>
                                                                    08:00 - 17:00
                                                                    </div>
                                                                </li>
                                                                <li>
                                                                <div className="days">
                                                                    Thursday
                                                                    </div>
                                                                    <div>
                                                                    08:00 - 17:00
                                                                    </div>
                                                                </li>
                                                                <li>
                                                                <div className="days">
                                                                    Friday
                                                                    </div>
                                                                    <div>
                                                                    08:00 - 17:00
                                                                    </div>
                                                                </li>
                                                                <li>
                                                                <div className="days">
                                                                    Saturday
                                                                    </div>
                                                                    <div>
                                                                    08:00 - 17:00
                                                                    </div>
                                                                </li>
                                                            </ul>
                                                        </div>
                                                        
                                                    </Paragraph>
                                                </p> 
                                                <p className="addressmainfield">
                                                    <div className="iconfield">
                                                        <MdPhoneIphone style={{color: "#1a2f52"}}/>
                                                    </div>                                                    
                                                    <Paragraph 
                                                    onClick={showphonenumber}
                                                    editable={{ onChange: setPhone }}>
                                                        {phone}
                                                    </Paragraph>
                                                </p> 
                                                <p className="addressmainfield margintop2">
                                                    <div className="iconfield">
                                                        <MdLanguage style={{color: "#1a2f52"}}/>
                                                    </div>                                                    
                                                    <Paragraph 
                                                    onClick={showwebsite}
                                                    editable={{ onChange: setEarth }}>
                                                        {earth}
                                                    </Paragraph>
                                                </p> 
                                                <p className="addressmainfield margintop2">
                                                    <div className="iconfield">
                                                        <MdEmail style={{color: "#1a2f52"}}/>
                                                    </div>                                                    
                                                    <Paragraph 
                                                    onClick={showemail}
                                                    editable={{ onChange: setMail }}>
                                                        {mail}
                                                    </Paragraph>
                                                </p> 
                                            </div>
                                        </div>                               
                                    </div>                                    
                                </Col>
                                <Col xs={24} sm={24} md={12} lg={12} xl={12} className="regionalsettingfield">
                                <div className="editfield">                                        
                                        <div className="companycontent">
                                            <h3 className="settingsheading regionalheading">
                                                Regional settings
                                            </h3>  
                                            <div className="addressfield">
                                                <p className="addressmainfield regionalmainfield">
                                                    <div className="regionalfield">
                                                        Country
                                                    </div>                                                   
                                                    <Paragraph 
                                                    onClick={showcountry}
                                                    editable={{ onChange: setCountry }}>
                                                        {country}
                                                    </Paragraph>
                                                </p> 
                                                <p className="addressmainfield regionalmainfield">
                                                    <div className="regionalfield">
                                                        Timezone
                                                    </div>                                                   
                                                    <Paragraph 
                                                    onClick={showtimezone}                                                                                                      
                                                    editable={{ onChange: setTimezone }}>
                                                        {timezone}
                                                    </Paragraph>
                                                </p> 
                                                <p className="addressmainfield regionalmainfield">
                                                    <div className="regionalfield">
                                                        Date format
                                                    </div>                                                   
                                                    <Paragraph 
                                                    onClick={showdateformat}
                                                    editable={{ onChange: setDateformat }}>
                                                        {dateformat}
                                                    </Paragraph>
                                                </p> 
                                                <p className="addressmainfield regionalmainfield">
                                                    <div className="regionalfield">
                                                        Time format
                                                    </div>                                                   
                                                    <Paragraph 
                                                    onClick={showtimeformat}  
                                                    editable={{ onChange: setTimeformat }}>
                                                        {timeformat}
                                                    </Paragraph>
                                                </p> 
                                                <p className="addressmainfield regionalmainfield">
                                                    <div className="regionalfield">
                                                        Language
                                                    </div>                                                   
                                                    <Paragraph 
                                                    onClick={showlanguage}                                                    
                                                    editable={{ onChange: setLanguage }}>
                                                        {language}
                                                    </Paragraph>
                                                </p> 
                                            </div>
                                        </div>                               
                                    </div> 
                                </Col>                    
                            </Row>
                            </TabPane>
                        <TabPane
                        tab={
                            <span>
                            <MdStars className="circlebg"/>
                                
                                Branding
                            </span>
                        }
                        key="2"
                        >
                            Branding
                        </TabPane>
                        <TabPane
                        tab={
                            <span>
                            <MdSupervisorAccount/>
                            {/* <img src={User} style={{fontSize: "36px",color: "#4c586d"}} className="circlebg"/> */}
                                Users
                            </span>
                        }
                        key="3"
                        >
                            Users
                        </TabPane>
                        <TabPane
                        tab={
                            <span>
                            <MdPayment/>
                                Online payment
                            </span>
                        }
                        key="4"
                        >
                            Online payment
                        </TabPane>
                        <TabPane
                        tab={
                            <span>
                            <MdEvent/>
                                Calendar
                            </span>
                        }
                        key="5"
                        >
                            Calendar
                        </TabPane>
                        <TabPane
                        tab={
                            <span>
                            <MdMonetizationOn/>
                                Finance
                            </span>
                        }
                        key="6"
                        >
                            Finance
                        </TabPane>
                        <TabPane
                        tab={
                            <span>
                            <BiListCheck/>
                            {/* <img src={Jobapplicatoin} style={{marginRight: "20px",width: "28px",margigLeft: "2px"}}/> */}
                                Job applications
                            </span>
                        }
                        key="7"
                        >
                            Job applications
                        </TabPane>
                        <TabPane
                        tab={
                            <span>
                            <MdForum/>
                                Communications
                            </span>
                        }
                        key="8"
                        >
                            Communications
                        </TabPane>
                        <TabPane
                        tab={
                            <span>
                            <MdAccountBox/>
                            Customer portal
                            </span>
                        }
                        key="9"
                        >
                            Customer portal
                        </TabPane>
                    </Tabs>
                    </div>
                </div>
                {/* <Drawer
                    title="Basic Drawer"
                    placement="left"
                    closable={false}
                    onClose={onClose}
                    visible={visible}
                >
                    <div className="tabfield">
                        <div className="ant-tabs-tab">
                            <span onClose={onClose}>
                                <MdBusiness style={{fontSize: "36px",color: "#4c586d"}}/>
                                Company profile
                            </span>
                        </div> 
                        <div className="ant-tabs-tab">
                            <span onClose={onClose}>
                                <MdStars style={{fontSize: "36px",color: "#4c586d"}} className="circlebg"/>                                
                                Branding
                            </span>
                        </div>                        
                    </div>
                </Drawer> */}
                <BusinesModal busines={editableStr} visible={businessModal} handleCancel={handleCancel} confirmChange={confirmBusinessChange}/>
                <BusinessServiceType busines={cleaningservice} visible={cleaningServiceModal} handleCancel={handleCancel} confirmChange={confirmCleaningChange}/>
                <ContactEmail busines={mail} visible={emilModal} handleCancel={handleCancel} confirmChange={confirmEmailChange}/>                
                <WebsiteAddress busines={earth} visible={websiteModal} handleCancel={handleCancel} confirmChange={confirmWebsiteChange}/>                
                <BusinessAddress address={address} visible={businessaddressModal} handleCancel={handleCancel} confirmChange={confirmBusinessAddressChange}/>                
                <PhoneNumberModal busines={phone} visible={phonenumberModal} handleCancel={handleCancel} confirmChange={confirmPhoneNumberChange}/>                
                <CountryModal busines={country} visible={countryModal} handleCancel={handleCancel} confirmChange={confirmCountryChange}/>                
                <LanguageModal busines={language} visible={languageModal} handleCancel={handleCancel} confirmChange={confirmLanguageChange}/>                
                <DateFormat busines={dateformat} visible={dateModal} handleCancel={handleCancel} confirmChange={confirmDateChange}/>                
                <TimeFormat busines={timeformat} visible={timeModal} handleCancel={handleCancel} confirmChange={confirmTimeFormatChange}/>                
                <TimeZoneModal busines={timezone} visible={timezoneModal} handleCancel={handleCancel} confirmChange={confirmtimezoneChange}/>                
                <BusinessHours busines={watch} visible={watchModal} handleCancel={handleCancel} confirmChange={confirmwatchChange}/>                
                
            </div>
        </div>
    )
}

export default Settings;







