import React, { useState } from 'react';
import { Modal, Button, Select, Input, Form, Col, Space  } from 'antd';

import { MinusCircleOutlined, PlusOutlined } from '@ant-design/icons';

import "./CreateCustomeField.css";
import { MdDeleteForever } from 'react-icons/md';
import { GiRoundStar } from 'react-icons/gi';
import { HiOutlineStar } from 'react-icons/hi';

const { Option } = Select;

function handleChange(value) {
    console.log(`selected ${value}`);
}








const CreateCustomeField = (props) => {

    const [toggleMenu, setToggleMenu] = useState(false);
    const [selectedValue, setValue] = useState('Text field');

    const onFinish = values => {
        console.log('Received values of form:', values);
      };

      const handleChangeDaf = (value) => {
          setValue(value);
      }

  return (
    <>
        <Modal
            title="CREATE CUSTOM FIELD"
            className="createcustomemodal"
            centered
            visible={props.visible}
            onOk={() => props.handleCancel()}
            onCancel={() => props.handleCancel()} 
            footer={null}           
        >

            <Button key="submit" type="primary" className="savebtncreatefield">
                Save
            </Button>



            <div className="create_custome_field">
                <p>Applied to</p>
                <div className="allcustomer_select bgselectcustomer">
                    <Select defaultValue="All customers" className="statusfield" onChange={handleChange}>
                        <Option value="All customers">All customers</Option>
                        <Option value="Paid">Paid</Option>
                        <Option value="Unpaid">Unpaid</Option>
                    </Select>
                </div>                
            </div>  
            <div className="create_custome_field">
                <Input placeholder="Custom field name" />             
            </div> 
            <div className="create_custome_field">
                <p>Field type</p>
                <div className="allcustomer_select">
                    <Select defaultValue={selectedValue} className="statusfield" onChange={handleChangeDaf}>
                        <Option value="Text field">Text field</Option>
                        <Option value="Dropdown" >Dropdown</Option>
                    </Select>
                </div>                 
            </div> 
            {
                selectedValue === "Text field" ? (
                    <div className="dropdown_serioul_number">
                        <div className="create_custome_field">
                            <div className="serioulfield">
                                <span>Example: Serial Number</span> <span className="serialnumberbtn">54A17-HEX</span>
                            </div>                               
                        </div>

                        <div className="create_custome_field">
                            <p>Dafault value</p>
                            <Input placeholder="Type here" />                  
                        </div>
                    </div>
                ): null
            }

            {
                selectedValue === "Dropdown" ? (
                    <div className="dropdown_lead_source">
                        <div className="create_custome_field">
                            <div className="serioulfield">
                                <span>Example: Lead source</span> 
                                <span className="serialnumberbtn">
                                    <Select defaultValue="Google" className="statusfield" onChange={handleChange}>
                                        <Option value="Google">Google</Option>
                                        <Option value="paid">paid</Option>
                                        <Option value="Unpaid">Unpaid</Option>
                                    </Select>
                                </span>
                            </div>                               
                        </div>
                        <div className="create_custome_field">
                                    <p>Options for dropdown</p>
                                        <Col span={24}>    
                                            <div class="field">
                                                <span className="fieldnumber">1.</span>
                                                <input type="text" name="Default option"  placeholder="Default option"/>                                                                          
                                            </div> 
                                        </Col>
                                        <Col span={24} style={{paddingTop: "9px"}}>  
                                            <div class="field">
                                                <span className="fieldnumber">2.</span>
                                                <input type="text" name="Option"  placeholder="Option"/>                                                                          
                                            </div>
                                        </Col>
                                        <Col span={24} style={{paddingTop: "9px",textAlign: "left"}} className="">
                                            <Form
                                            // initialValues={{ users: [{fieldKey: 0, isListField: true, key: 0, name: 0}] }}
                                            name="dynamic_form_nest_item" onFinish={onFinish} autoComplete="off">                                        
                                            <Form.List name="users">
                                                {(fields, { add, remove }) => {
                                                return (
                                                    <div className="contactadd">
                                                        {fields.map((fieldsection, index) => (
                                                            <Space key={fieldsection.key} style={{ display: 'flex', marginBottom: 9 }} align="start" className="maincontact">
                                                                    <div class="field">
                                                                        <span className="fieldnumber">{index+3}.</span>
                                                                        <input type="text" name="Option"  placeholder="Option"/> 
                                                                    </div>
                                                                    <div className="deletbtnfield">
                                                                        <MdDeleteForever
                                                                            style={{color: '#f55555', fontSize: '24px', cursor: "pointer"}}
                                                                            onClick={() => {
                                                                            remove(fieldsection.name);
                                                                            }}
                                                                        /> 
                                                                    </div>                                                                                                                  
                                                            </Space>
                                                        ))}
                                                            <Form.Item>
                                                                <h5 
                                                                type="dashed"
                                                        onClick={() => {
                                                            add();
                                                        }}
                                                        block className="inputbottomheading"><PlusOutlined /> Add another option</h5>
                                                    </Form.Item>
                                            </div>
                                        );
                                        }}
                                    </Form.List>
                                    </Form>
                                </Col>
                </div>
            </div>
                ): null
            }
                 
            
        </Modal>
    </>
  );
}
export default CreateCustomeField;