import React, { useState, useEffect } from 'react'
import { Row, Col,Modal, Input, Button, Select } from 'antd';

import "./BusinessHours.css";

const { Option } = Select;

const options = [
    {
      value: 'zhejiang',
      label: 'Zhejiang',
      children: [
        {
          value: 'hangzhou',
          label: 'Hangzhou',
          children: [
            {
              value: 'xihu',
              label: 'West Lake',
            },
          ],
        },
      ],
    },
    {
      value: 'jiangsu',
      label: 'Jiangsu',
      children: [
        {
          value: 'nanjing',
          label: 'Nanjing',
          children: [
            {
              value: 'zhonghuamen',
              label: 'Zhong Hua Men',
            },
          ],
        },
      ],
    },
  ];

const BusinessHours = ({busines, visible, handleCancel, confirmChange}) => {
    const [value, settimezone] = useState('');
    const handleOk = () => {
        confirmChange(value);
    }
    useEffect(() => {
        settimezone(busines)
    }, [visible])
    return (
        <div>
                <Modal
                    className="businessmodel responsivebtn businesshours"
                    title="Edit business hours"
                    centered
                    visible={visible}
                    onOk={handleOk}
                    onCancel={handleCancel}
                    footer={null}
                    >
                        <div>
                            <ul>
                                <li>
                                    <div>
                                        <h4>Sunday</h4>
                                        <h5>Closed</h5>
                                    </div>
                                </li>
                            </ul>
                        </div>
                        <div class="businessaddressbtn">
                            <Button key="submit" type="primary" onClick={handleOk}>
                                Confirm
                            </Button>
                            <Button key="back" onClick={handleCancel}>
                                Cancel
                            </Button>                                            
                        </div> 
                </Modal>
        </div>
        
    )
}

export default BusinessHours;

