import React, {useState } from 'react'
import { Row, Col, Table, Button, Switch, Space, Input, Value, Select,Tag, Menu, Dropdown, DatePicker  } from 'antd';
import {MdPersonAdd ,MdMoreVert, MdAssignmentTurnedIn, MdKeyboardArrowDown, MdSearch, MdKeyboardArrowRight, MdKeyboardArrowLeft} from "react-icons/md";
import {Link} from "react-router-dom";

import 'antd/dist/antd.css';

import BuildCircle from "../../Assets/images/build_circle.svg";
import DeletMenu from "../../Assets/images/deletmenu.svg";
import EditMenu from "../../Assets/images/editmenu.svg";
import EyeMenu from "../../Assets/images/eyemenu.svg";

import { RiFolderDownloadLine } from 'react-icons/ri';
import { BiExport } from 'react-icons/bi';



import { AiOutlineEye } from "react-icons/ai";


// import Header from '../Header/Header';
// import Footer from '../Footer/Footer';


import "./CustomerSchedule.css";

const { Option } = Select;

const { Search } = Input;


function handleChange(value) {
    console.log(`selected ${value}`);
}


const columns = [
    {
        title: 'Status',
        key: 'tags',
        dataIndex: 'tags',
        render: tags => (
          <>
            {tags.map(tag => {
              let color = tag.length > 5 ? 'geekblue' : 'green';
              if (tag === 'Scheduled') {                
                color = 'blue';
              }
              if (tag === 'Unscheduled') {
                color = 'red';
              }
              return (
                // <Tag className={className} key={tag} className="tagoption">
                <React.Fragment>
                    <Space size="middle" className="actionfield">
                            {tag === 'Scheduled' ? 
                            (<a><img src={BuildCircle} className="settingcircle"/></a>) :
                            <a style={{display: "flex"}}><MdAssignmentTurnedIn className="assignmenticon"/></a> }
                        </Space>

                    <Tag color={color} key={tag} className="tagoption">
                        {tag}
                    </Tag>
                </React.Fragment>
              );
            })}
          </>
        ),
      },
    {
      title: 'Number',
      dataIndex: 'number',
      key: 'number',
      render: text => <Link to="">{text}</Link>
    },
    {
      title: 'Date',
      dataIndex: 'date',
      key: 'date',
    },
    {
      title: 'Amount',
      dataIndex: 'amount',
      key: 'amount',
    },    
    {
      title: 'Actions',
      dataIndex: 'action',
      key: 'action',
      render: (text, record) => (
        <Space size="middle" className="approvefield">
          <a>Send reminder{record.name}</a>
          <MdKeyboardArrowDown className="actionarrow"/>
        </Space>
      ),
    },
  ];
  
  const data = [
    {
      key: '1',
      tags: ['Scheduled'],
      number: 3492,
      date: '8-9-2020',
      amount: '$550.00',
      action: "Send reminder"
    },
    {
      key: '2',
      tags: ['Unscheduled'],
      number: 3460,
      date: '4-9-2020',
      amount: '$950.00' ,
      action: "Schedule now"     
    }
];


const CustomerSchedule = () => { 

    const [focusName, setFocusName] = useState(false);

return (
        <div className="customer_form">
            <div className="customer_section" style={{marginBottom: "20px"}}>
                <div className="finacialdatefield">
                    <div className="container">
                        <div className="finacicaldatesection">
                            <ul>
                                <li>
                                    <Select defaultValue="All Status" className="statusfield" onChange={handleChange}>
                                        <Option value="All Status">All Status</Option>
                                        <Option value="Paid">Paid</Option>
                                        <Option value="Unpaid">Unpaid</Option>
                                    </Select>
                                </li>
                                <li className="datepickersection">
                                    <Input.Group compact>
                                        <DatePicker style={{ width: '50%' }} />
                                        <DatePicker style={{ width: '50%' }}  />
                                    </Input.Group>
                                </li>
                                <li className="tableserachsection">
                                    <Search
                                    className="searchinvoice"
                                    placeholder="Search job & estimate"
                                    onSearch={value => console.log(value)}
                                    // style={{ width: 320 }}
                                    />
                                </li>
                                <li className="unpaidinvoice">
                                    <span>
                                        <Link>Jobs</Link>
                                    </span>
                                    <span>
                                        <Link>Estimates</Link>
                                    </span>
                                    <span className="invoicebill">
                                        <Link>All tasks</Link>
                                    </span>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
                <div className="container">
                    <div className="customerfinacilatable">
                        <Table 
                        scroll={{ x: 400}}
                        className="customertable" 
                        columns={columns} 
                        dataSource={data} 
                        style={{ whiteSpace: "pre-line" }}
                        />
                    </div>
                    <div className="paginationmainfield">
                        <div>
                            <b className="showfield">Show</b>
                            <Select defaultValue="10" style={{width: "75px"}} onChange={handleChange}>
                                <Option value="10">10</Option>
                                <Option value="25">25</Option>
                                <Option value="50">50</Option>
                            </Select>
                            <b className="perpagefield">per page</b>
                        </div> 
                        <div className="paginationfield">
                            <ul>
                                <li className="ant-pagination-total-text">3-3 <span>of</span> 1</li>
                                <li className="ant-pagination-prev"><MdKeyboardArrowLeft/></li>
                                <li className="ant-pagination-next"><MdKeyboardArrowRight/></li>
                            </ul>
                        </div>                       
                    </div>
                </div>
            </div>
        </div>
        )
}

export default CustomerSchedule;