import React, { useState, useEffect } from 'react'
import { Row, Col,Modal, Input, Button, Select } from 'antd';

import "./WebsiteAddress.css";


const { Option } = Select;

const WebsiteAddress = ({busines, visible, handleCancel, confirmChange}) => {
    const [value, setWebsite] = useState('');
    const handleOk = () => {
        confirmChange(value);
    }
    useEffect(() => {
        setWebsite(busines)
    }, [visible])
    return (
        <div>
                <Modal
                    className="businessmodel responsivebtn"
                    title="Edit website address"
                    centered
                    visible={visible}
                    onOk={handleOk}
                    onCancel={handleCancel}
                    // footer={[
                    //     <Button key="back" onClick={handleCancel}>
                    //       Cancel
                    //     </Button>,
                    //     <Button key="submit" type="primary" onClick={handleOk}>
                    //       Confirm
                    //     </Button>,
                    // ]}
                    footer={null}
                    >
                        <div className="webisteaddressfield mainwebsiteaddress">
                            <Select defaultValue="https" style={{ marginRight: '1.2rem' }}>
                                <Option value="https">https</Option>
                                <Option value="http">http</Option>
                                <Option value="IMAP">IMAP</Option>
                                <Option value="FTP">FTP</Option>
                            </Select> 
                            <Input value={value} onChange={(e)=>setWebsite(e.target.value)} placeholder="Basic usage" style={{padding: ".7rem 1.4rem"}}/>
                        </div>
                        <div class="businessaddressbtn">
                            <Button key="submit" type="primary" onClick={handleOk}>
                                Confirm
                            </Button>
                            <Button key="back" onClick={handleCancel}>
                                Cancel
                            </Button>                                            
                        </div> 
                        
                </Modal>
        </div>
        
    )
}

export default WebsiteAddress;

