import React, { useState, useEffect } from 'react'
import { Row, Col,Modal, Input, Button, Select } from 'antd';
import ReactFlagsSelect from 'react-flags-select';

import 'react-flags-select/css/react-flags-select.css';
import "./PhoneNumberModal.css";



const { Option } = Select;

const PhoneNumberModal = ({busines, visible, handleCancel, confirmChange}) => {
    const [value, setPhoneNumber] = useState('');
    const handleOk = () => {
        confirmChange(value);
    }
    useEffect(() => {
        setPhoneNumber(busines)
    }, [visible])
    return (
        <div>
                <Modal
                    className="businessmodel responsivebtn"
                    title="Edit phone number"
                    centered
                    visible={visible}
                    onOk={handleOk}
                    onCancel={handleCancel}
                    footer={null}
                    >
                        <p className="forresponsive">Updating will change your phone number for some Jobpatcher services. <a href="">Learn more</a></p>
                        <div className="webisteaddressfield phonefieldsection">
                            <ReactFlagsSelect className="menu-flags" defaultCountry="US" showSelectedLabel={false} />
                            <Input value={value} onChange={(e)=>setPhoneNumber(e.target.value)} placeholder="Basic usage" style={{padding: "7px 14px"}}/>
                        </div>  
                        <div class="businessaddressbtn">
                            <Button key="submit" type="primary" onClick={handleOk}>
                                Confirm
                            </Button>
                            <Button key="back" onClick={handleCancel}>
                                Cancel
                            </Button>                                            
                        </div>                       
                </Modal>
        </div>
        
    )
}

export default PhoneNumberModal;

