import React, { useState, useEffect } from 'react'
import { Row, Col,Modal, Input, Button, Select } from 'antd';
import ReactFlagsSelect from 'react-flags-select';

import 'react-flags-select/css/react-flags-select.css';
import "./CountryModal.css";



const { Option } = Select;

const options = [
    {
      value: 'zhejiang',
      label: 'Zhejiang',
      children: [
        {
          value: 'hangzhou',
          label: 'Hangzhou',
          children: [
            {
              value: 'xihu',
              label: 'West Lake',
            },
          ],
        },
      ],
    },
    {
      value: 'jiangsu',
      label: 'Jiangsu',
      children: [
        {
          value: 'nanjing',
          label: 'Nanjing',
          children: [
            {
              value: 'zhonghuamen',
              label: 'Zhong Hua Men',
            },
          ],
        },
      ],
    },
  ];

const CountryModal = ({busines, visible, handleCancel, confirmChange}) => {
    const [toggleMenu, setToggleMenu] = useState(false);
    const [value, setCountry] = useState('');
    const handleOk = () => {
        confirmChange(value);
    }
    useEffect(() => {
        setCountry(busines)
    }, [visible])
    return (
        <div>
                <Modal
                    className="businessmodel responsivebtn"
                    title="Change country"
                    centered
                    visible={visible}
                    onOk={handleOk}
                    onCancel={handleCancel}
                    footer={null}
                    >
                        <div className="countryfield" onClick={()=>setToggleMenu(!toggleMenu)}>                            
                            <ReactFlagsSelect className={toggleMenu ? "menu-flags open" : "menu-flags"} defaultCountry="CA" onChange={(selectedOption)=>setCountry(selectedOption)}/>                            
                        </div>  
                        <div class="businessaddressbtn">
                            <Button key="submit" type="primary" onClick={handleOk}>
                                Confirm
                            </Button>
                            <Button key="back" onClick={handleCancel}>
                                Cancel
                            </Button>                                            
                        </div>                       
                </Modal>
        </div>
        
    )
}

export default CountryModal;

