import React, { useState, useEffect } from 'react'
import { Row, Col,Modal, Input, Button } from 'antd';

import "./ContactEmail.css";

const ContactEmail = ({busines, visible, handleCancel, confirmChange}) => {
    const [value, setEmail] = useState('');
    const handleOk = () => {
        confirmChange(value);
    }
    useEffect(() => {
        setEmail(busines)
    }, [visible])
    return (
        <div>
                <Modal
                    className="businessmodel responsivebtn"
                    title="Edit contact email"
                    centered
                    visible={visible}
                    onOk={handleOk}
                    onCancel={handleCancel}
                    footer={null}
                    // footer={[
                    //     <Button key="back" onClick={handleCancel}>
                    //       Cancel
                    //     </Button>,
                    //     <Button key="submit" type="primary" onClick={handleOk}>
                    //       Confirm
                    //     </Button>,
                    // ]}
                    >
                        <Input value={value} onChange={(e)=>setEmail(e.target.value)} placeholder="Basic usage" style={{padding: ".7rem 1.4rem"}}/>
                        <div class="businessaddressbtn">
                            <Button key="submit" type="primary" onClick={handleOk}>
                                Confirm
                            </Button>
                            <Button key="back" onClick={handleCancel}>
                                Cancel
                            </Button>                                            
                        </div> 
                </Modal>
        </div>
        
    )
}

export default ContactEmail;

