import React, { useState, useEffect } from 'react'
import { Row, Col,Modal, Input, Button, Select } from 'antd';

import "./BusinessServiceType.css";

const { Option } = Select;

const options = [
    {
      value: 'zhejiang',
      label: 'Zhejiang',
      children: [
        {
          value: 'hangzhou',
          label: 'Hangzhou',
          children: [
            {
              value: 'xihu',
              label: 'West Lake',
            },
          ],
        },
      ],
    },
    {
      value: 'jiangsu',
      label: 'Jiangsu',
      children: [
        {
          value: 'nanjing',
          label: 'Nanjing',
          children: [
            {
              value: 'zhonghuamen',
              label: 'Zhong Hua Men',
            },
          ],
        },
      ],
    },
  ];

const BusinesModal = ({busines, visible, handleCancel, confirmChange}) => {
    const [value, setcleaning] = useState('');
    const handleOk = () => {
        confirmChange(value);
    }
    useEffect(() => {
        setcleaning(busines)
    }, [visible])
    return (
        <div>
                <Modal
                    className="businessmodel responsivebtn"
                    title="Business service type"
                    centered
                    visible={visible}
                    onOk={handleOk}
                    onCancel={handleCancel}
                    footer={null}
                    >
                      <p className="forresponsive">Jobpatcher will automatically adjust features and imporat basic service and materials pricing catalogs</p>
                        <Select defaultValue="Cleaning Service" onChange={(selectedOption)=>setcleaning(selectedOption)} style={{ width: '100%' }} >
                            <Option value="Appliance Repair"  >Appliance Repair</Option>
                            <Option value="Cleaning Service">Cleaning Service</Option>
                            <Option value="Carpet Cleaning">Carpet Cleaning</Option>
                            <Option value="Electrical Contracting">Electrical Contracting</Option>
                            <Option value="Elevator Services">Elevator Services</Option>
                            <Option value="Garage Door Services">Garage Door Services</Option>
                            <Option value="General Contracting">General Contracting</Option>
                        </Select> 
                        {/* <Input  placeholder="Basic usage" style={{padding: ".7rem 1.4rem"}}/> */}
                        <div class="businessaddressbtn">
                            <Button key="submit" type="primary" onClick={handleOk}>
                                Confirm
                            </Button>
                            <Button key="back" onClick={handleCancel}>
                                Cancel
                            </Button>                                            
                        </div> 
                </Modal>
        </div>
        
    )
}

export default BusinesModal;

