import React, { useState, useEffect } from 'react'
import { Row, Col,Modal, Input, Button, Select } from 'antd';

import "./TimeZoneModal.css";

const { Option } = Select;

const options = [
    {
      value: 'zhejiang',
      label: 'Zhejiang',
      children: [
        {
          value: 'hangzhou',
          label: 'Hangzhou',
          children: [
            {
              value: 'xihu',
              label: 'West Lake',
            },
          ],
        },
      ],
    },
    {
      value: 'jiangsu',
      label: 'Jiangsu',
      children: [
        {
          value: 'nanjing',
          label: 'Nanjing',
          children: [
            {
              value: 'zhonghuamen',
              label: 'Zhong Hua Men',
            },
          ],
        },
      ],
    },
  ];

const TimeZoneModal = ({busines, visible, handleCancel, confirmChange}) => {
    const [value, settimezone] = useState('');
    const handleOk = () => {
        confirmChange(value);
    }
    useEffect(() => {
        settimezone(busines)
    }, [visible])
    return (
        <div>
                <Modal
                    className="businessmodel responsivebtn"
                    title="Change time format"
                    centered
                    visible={visible}
                    onOk={handleOk}
                    onCancel={handleCancel}
                    footer={null}
                    >
                        <Select defaultValue="(UTC-5) Central Daylight Time	" onChange={(selectedOption)=>settimezone(selectedOption)} style={{ width: '100%' }} >
                            <Option value="(UTC-5) Central Daylight Time">(UTC-5) Central Daylight Time	</Option>
                            <Option value="(UTC-5) Eastern Standard Time">(UTC-5) Eastern Standard Time</Option>
                            <Option value="(UTC-4) Eastern Daylight Time">(UTC-4) Eastern Daylight Time</Option>
                            <Option value="(UTC-4) Atlantic Standard Time">(UTC-4) Atlantic Standard Time</Option>
                            <Option value="(UTC-3) Atlantic Daylight Time	">(UTC-3) Atlantic Daylight Time	</Option>                            
                        </Select> 
                        {/* <Input  placeholder="Basic usage" style={{padding: ".7rem 1.4rem"}}/> */}
                        <div class="businessaddressbtn">
                            <Button key="submit" type="primary" onClick={handleOk}>
                                Confirm
                            </Button>
                            <Button key="back" onClick={handleCancel}>
                                Cancel
                            </Button>                                            
                        </div> 
                </Modal>
        </div>
        
    )
}

export default TimeZoneModal;

