import React, { useState, useEffect } from 'react'
import { Row, Col,Modal, Input, Button } from 'antd';

import "./BusinessModal.css";

const BusinesModal = ({busines, visible, handleCancel, confirmChange}) => {
    const [value, setBusiness] = useState('');
    const handleOk = () => {
        confirmChange(value);
    }
    useEffect(() => {
        setBusiness(busines)
    }, [visible])
    return (
        <div>
                <Modal
                    className="businessmodel responsivebtn"
                    title="Edit business name"
                    centered
                    visible={visible}
                    onOk={handleOk}
                    onCancel={handleCancel}
                    footer={null}
                    >
                        <p className="forresponsive">Business name will be used on the estimate and invoice you send to your customers</p>
                        <Input value={value} onChange={(e)=>setBusiness(e.target.value)} placeholder="Basic usage" style={{padding: ".7rem 1.4rem"}}/>
                        <div class="businessaddressbtn">
                            <Button key="submit" type="primary" onClick={handleOk}>
                                Confirm
                            </Button>
                            <Button key="back" onClick={handleCancel}>
                                Cancel
                            </Button>                                            
                        </div> 
                </Modal>
        </div>
        
    )
}

export default BusinesModal;

