import React, {useState } from 'react'
import { Row, Col, Table, Button, Switch, Space, Input, Value, Select,Tag, Menu, Dropdown } from 'antd';
import {MdPersonAdd ,MdMoreVert, MdAssignmentTurnedIn, MdKeyboardArrowDown, MdSearch, MdKeyboardArrowRight, MdKeyboardArrowLeft} from "react-icons/md";
import {Link} from "react-router-dom";

import 'antd/dist/antd.css';

import BuildCircle from "../../Assets/images/build_circle.svg";
import DeletMenu from "../../Assets/images/deletmenu.svg";
import EditMenu from "../../Assets/images/editmenu.svg";
import EyeMenu from "../../Assets/images/eyemenu.svg";

import { RiFolderDownloadLine } from 'react-icons/ri';
import { BiExport } from 'react-icons/bi';



import { AiOutlineEye } from "react-icons/ai";


// import Header from '../Header/Header';
// import Footer from '../Footer/Footer';


import "./Customers.css";

const { Option } = Select;

const { Search } = Input;


function handleChange(value) {
    console.log(`selected ${value}`);
}


const data = [
    {
        key: 1,
        name: 'Peter Jonson',
        address: '2655 Speers Road\n Oakville, ON L6J 3X4',
        mobile: "+1 (546) 780-2263",
        email: "peter@peterdigico.com",
        company: "Peter Digital Co."
    },
    {
        key: 2,
        name: 'Joe Rosdo',
        address: '4780 Wallace Street\n Nanaimo, BC V9R 3A8',
        mobile: "+1 (546) 788-0349 ",
        email: "jeorosdo@yahoo.com",
        company: "Joe Digital Co."
    },
    {
        key: 3,
        name: 'Robert Lennox',
        address: '3350 Lauzon Parkway\n Thetford Mines, QC N9N 9N9',
        mobile: "+1 (546) 347-9022",
        email: "robertmlennox@rhyta.com",
        company: "Robert MLennox Inc."
    },
];





const rowSelection = {
    onChange: (selectedRowKeys, selectedRows) => {
    console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
    },
    onSelect: (record, selected, selectedRows) => {
    console.log(record, selected, selectedRows);
    },
    onSelectAll: (selected, selectedRows, changeRows) => {
    console.log(selected, selectedRows, changeRows);
    },
};




const Customers = () => {

    const [checkStrictly, setCheckStrictly] = React.useState(false);

    const [dataSource, setDataSource] = useState(data);

    const [value, setValue] = useState('');

    const FilterByNameInput = (
        <div className="searchfieldsection">        
            <Input
            onFocused
            style={{ width: 209 }}
            placeholder="Search"
            value={value}
            // value="Search"
            className="tablesearchfield"
            onChange={e => {
                const currValue = e.target.value;
                setValue(currValue);
                const filteredData = data.filter(entry =>
                entry.name.includes(currValue)
                );
                setDataSource(filteredData);
            }}
            />
             {/* <Search
                placeholder="input search text"
                onSearch={value => console.log(value)}
                style={{ width: 200 }}                
                /> */}
            <MdSearch className="searchiconfield"/>
            <Select defaultValue="All status" style={{ width: 120, marginLeft: "7px" }} onChange={handleChange}>
                <Option value="All status">All status</Option>
                <Option value="Active">Active</Option>
                <Option value="InActive">InActive</Option>
            </Select>
    </div>
    
    );

   

  


  const columns = [
        {
            title: FilterByNameInput,
            dataIndex: 'name',
            key: 1,
            // width: "33%",
            render: (text, record) => {
                console.log("=====",record)
            return <Link to="/customerprofile"><span className="maincustomername">{record.name}</span><Tag className="companyname">{record.company}</Tag><Tag className="activefield">{record.key % 2 === 0 ? 'InActive': 'Active'}</Tag></Link>
        }

           
        },

       
        {
            title: 'Address',
            dataIndex: 'address',
            key: 'address',
            // width: '20%',
        },
        {
            title: 'Mobile',
            dataIndex: 'mobile',
            key: 'mobile',
            width: '13%',
        },
        {
            title: 'Email',
            dataIndex: 'email',
            key: 'email',
            // width: '',
        },
        {
            title: 'Actions',
            dataIndex: 'actions',
            key: 'actions',
            // width: '17%',
            render: (text, record) => (
                <Space size="middle" className="actionfield">
                    <a><img src={BuildCircle} className="settingcircle"/></a>
                    <a style={{display: "flex"}}><MdAssignmentTurnedIn className="assignmenticon"/></a>
                    <Dropdown overlay={menu} trigger={['click']}>
                        <a className="viewtable" onClick={e => e.preventDefault()}>View All <MdKeyboardArrowDown/></a>                   
                    </Dropdown>
                </Space>
            ),
        },    
  ];


  const menu = (
    <Menu className="viewmenu">
      <Menu.Item key="0">
        <Link to=""> <img src={EyeMenu}/>View</Link>
      </Menu.Item>
      <Menu.Item key="1">
        <Link to=""><img src={EditMenu} />Edit</Link>
      </Menu.Item>
      <Menu.Item key="2">
        <Link to=""> <img src={DeletMenu} />Delet</Link>
      </Menu.Item>
    </Menu>
  );

  const exportmenu = (
    <Menu className="viewmenu">
      <Menu.Item key="0">
        <Link to=""> <RiFolderDownloadLine/>Import</Link>
      </Menu.Item>
      <Menu.Item key="1">
        <Link to=""> <BiExport/>Export</Link>
      </Menu.Item>
    </Menu>
  );

return (
        <div className="customer_form">
            <div className="customer_section" style={{marginBottom: "20px"}}>
                <div className="maincustomerfield">
                    <div className="container">
                        <div className="customerheaingfield">
                        <h3 className="pageheading">3 Customers</h3>
                        <div className="customerbtnfield">
                            <div className="addcustomerbtn">
                                <Link to="addcustomer">
                                <MdPersonAdd/>
                                NEW CUSTOMER
                                </Link>
                            </div>
                            <div className="morebtnfield">
                                <Dropdown overlay={exportmenu} trigger={['click']}>
                                        <a onClick={e => e.preventDefault()}><MdMoreVert/></a>                                       
                                </Dropdown>
                            </div>
                        </div>
                        </div>
                    </div>
                </div>
                <div className="container">
                    <div>
                    <Table
                    scroll={{ x: 1000}}
                    className="customertable"
                    style={{ whiteSpace: "pre-line" }}
                    columns={columns}
                    rowSelection={{ ...rowSelection, checkStrictly }}
                    dataSource={dataSource}
                    // pagination={{
                    //     total: dataSource.length,
					//     pageSize: 10,
                    //     showTotal: (total, range) => `${range[0]}-${range[1]} of ${total}`,
                    // }}
                    />
                    </div>
                    <div className="paginationmainfield">
                        <div>
                            <b className="showfield">Show</b>
                            <Select defaultValue="25" style={{width: "75px"}} onChange={handleChange}>
                                <Option value="25">25</Option>
                                <Option value="50">50</Option>
                                <Option value="100">100</Option>
                            </Select>
                            <b className="perpagefield">per page</b>
                        </div> 
                        <div className="paginationfield">
                            <ul>
                                <li className="ant-pagination-total-text">3-3 <span>of</span> 1</li>
                                <li className="ant-pagination-prev"><MdKeyboardArrowLeft/></li>
                                <li className="ant-pagination-next"><MdKeyboardArrowRight/></li>
                            </ul>
                        </div>                       
                    </div>
                </div>
            </div>
        </div>
        )
}

export default Customers;