import React from 'react'
import { Card, Input, Col, Row, Select, Checkbox,Collapse  } from 'antd';
import "./AdditionalPropertyDetails.css";

const { Panel } = Collapse;
const { TextArea } = Input;

const AdditionalPropertyDetails = () => {
    return (
        <div className="boxfield">
            <Collapse accordion>
                <Panel header="Additional property details" key="1">
                        <Row gutter={12}>
                            <Col span={12}>
                                <label>Alarm code</label>
                                <TextArea rows={4} />
                            </Col>
                            <Col span={12}>
                                <label>Key note</label>
                                <TextArea rows={4} />
                            </Col>                            
                        </Row>
                </Panel>
            </Collapse>
        </div>
    )
}

export default AdditionalPropertyDetails;
