import React, { useState } from 'react';
import { Card, Input, Col, Row, Select, Checkbox } from 'antd';
import "./PropertyDetails.css";

const { Option } = Select;

function onChange(e) {
    console.log(`checked = ${e.target.checked}`);
}

const options = [
    {
      value: 'zhejiang',
      label: 'Zhejiang',
      children: [
        {
          value: 'hangzhou',
          label: 'Hangzhou',
          children: [
            {
              value: 'xihu',
              label: 'West Lake',
            },
          ],
        },
      ],
    },
    {
      value: 'jiangsu',
      label: 'Jiangsu',
      children: [
        {
          value: 'nanjing',
          label: 'Nanjing',
          children: [
            {
              value: 'zhonghuamen',
              label: 'Zhong Hua Men',
            },
          ],
        },
      ],
    },
  ];

const PropertyDetails = () => {
    const [toggleMenu, setToggleMenu] = useState(false);
    return (
        <div className="boxfield">
            <Card title="Property details">
                        <Row gutter={12}>                                
                                <Col xs={24} sm={24} md={24} lg={24} xl={24}>
                                    <div class="field">
                                        <input type="text" name="Street1" id="Street1" placeholder="Street 1"/>
                                        <div className="companyfield">
                                            <label for="Street1">Street 1</label>
                                        </div>                                     
                                    </div>
                                </Col>
                                <Col span={24} style={{paddingTop: "12px"}}>
                                    <div class="field">
                                        <input type="text" name="Street2" id="Street2" placeholder="Street 2"/>
                                        <div className="companyfield">
                                            <label for="Street2">Street 2</label>
                                        </div>                                     
                                    </div>
                                </Col>
                                <Col xs={24} sm={12} md={12} lg={12} xl={12} style={{paddingTop: "12px"}}>
                                    <div className="inputfieldbox">
                                        <div class="field">
                                            <input type="text" name="City" id="City" placeholder="City"/>
                                            <div className="companyfield">
                                                <label for="City">City</label>
                                            </div>                                     
                                        </div>
                                    </div>  
                                </Col>
                                <Col xs={24} sm={12} md={12} lg={12} xl={12} style={{paddingTop: "12px"}}>
                                    <div className="inputfieldbox">
                                        <div class="field">
                                            <input type="text" name="State" id="State" placeholder="State"/>
                                            <div className="companyfield">
                                                <label for="State">State</label>
                                            </div>                                     
                                        </div>
                                    </div>                                    
                                </Col>
                                <Col xs={24} sm={12} md={12} lg={12} xl={12} style={{paddingTop: "12px"}}>
                                    <div className="inputfieldbox">
                                        <div class="field">
                                            <input type="text" name="Zipcode" id="Zipcode" placeholder="Zip code"/>
                                            <div className="companyfield">
                                                <label for="Zipcode">Zip code</label>
                                            </div>                                     
                                        </div>
                                    </div> 
                                </Col>
                                <Col xs={24} sm={12} md={12} lg={12} xl={12} style={{paddingTop: "12px"}}>
                                    <Select defaultValue="Canada" style={{ marginRight: '1.2rem' }}>
                                        <Option value="Canada">Canada</Option>
                                        <Option value="Jiangsu">Jiangsu</Option>
                                    </Select>                                                                        
                                </Col>
                                <Col xs={24} sm={24} md={24} lg={24} xl={24} style={{paddingTop: "18px",textAlign: "left"}} className="checkboxfield">
                                    <Checkbox checked={!toggleMenu} onChange={onChange} onClick={()=>setToggleMenu(!toggleMenu)}>Billing address is the same as property address</Checkbox>
                                </Col>
                                <div className={toggleMenu ? "billingaddres open" : "billingaddres"}>
                                    <Col span={24} style={{paddingTop: "26px",textAlign: "left",paddingBottom: "29px"}} className="">
                                        <h4 className="boxsubheading">Billing address</h4>
                                    </Col>
                                    <Col xs={24} sm={24} md={24} lg={24} xl={24}>
                                        <div class="field">
                                            <input type="text" name="Street1" id="Street1" placeholder="Street 1"/>
                                            <div className="companyfield">
                                                <label for="Street1">Street 1</label>
                                            </div>                                     
                                        </div>
                                    </Col>
                                    <Col span={24} style={{paddingTop: "12px"}}>
                                        <div class="field">
                                            <input type="text" name="Street2" id="Street2" placeholder="Street 2"/>
                                            <div className="companyfield">
                                                <label for="Street2">Street 2</label>
                                            </div>                                     
                                        </div>
                                    </Col>
                                    <Col xs={24} sm={12} md={12} lg={12} xl={12} style={{paddingTop: "12px"}}>
                                        <div className="inputfieldbox">
                                            <div class="field">
                                                <input type="text" name="City" id="City" placeholder="City"/>
                                                <div className="companyfield">
                                                    <label for="City">City</label>
                                                </div>                                     
                                            </div>
                                        </div>  
                                    </Col>
                                    <Col xs={24} sm={12} md={12} lg={12} xl={12} style={{paddingTop: "12px"}}>
                                        <div className="inputfieldbox">
                                            <div class="field">
                                                <input type="text" name="State" id="State" placeholder="State"/>
                                                <div className="companyfield">
                                                    <label for="State">State</label>
                                                </div>                                     
                                            </div>
                                        </div>                                    
                                    </Col>
                                    <Col xs={24} sm={12} md={12} lg={12} xl={12} style={{paddingTop: "12px"}}>
                                        <div className="inputfieldbox">
                                            <div class="field">
                                                <input type="text" name="Zipcode" id="Zipcode" placeholder="Zip code"/>
                                                <div className="companyfield">
                                                    <label for="Zipcode">Zip code</label>
                                                </div>                                     
                                            </div>
                                        </div> 
                                    </Col>
                                    <Col xs={24} sm={12} md={12} lg={12} xl={12} style={{paddingTop: "12px"}}>
                                    <Select defaultValue="Canada" style={{ marginRight: '1.2rem' }}>
                                        <Option value="Canada">Canada</Option>
                                        <Option value="Jiangsu">Jiangsu</Option>
                                    </Select>                                                                        
                                </Col>
                                </div>
                        </Row>   
            </Card>
        </div>
    )
}

export default PropertyDetails;
