import React, {useState } from 'react'
import { Row, Col, Table, Button, Switch, Space, Input, Value, Select,Tag, Menu, Dropdown, DatePicker  } from 'antd';
import {MdPersonAdd ,MdMoreVert, MdAssignmentTurnedIn, MdKeyboardArrowDown, MdSearch, MdKeyboardArrowRight, MdKeyboardArrowLeft} from "react-icons/md";
import {Link} from "react-router-dom";

import 'antd/dist/antd.css';

import BuildCircle from "../../Assets/images/build_circle.svg";
import DeletMenu from "../../Assets/images/deletmenu.svg";
import EditMenu from "../../Assets/images/editmenu.svg";
import EyeMenu from "../../Assets/images/eyemenu.svg";

import { RiFolderDownloadLine } from 'react-icons/ri';
import { BiExport } from 'react-icons/bi';



import { AiOutlineEye } from "react-icons/ai";


// import Header from '../Header/Header';
// import Footer from '../Footer/Footer';


import "./CustomerHistory.css";

const { Option } = Select;

const { Search } = Input;


function handleChange(value) {
    console.log(`selected ${value}`);
}


const columns = [
    {
        title: 'Status',
        key: 'tags',
        dataIndex: 'tags',  
        render: tags => (
          <>
            {tags.map(tag => {
              let color = tag.length > 5 ? 'geekblue' : 'green';
              if (tag === 'Converted') {                
                color = 'blue';
              }
              if (tag === 'Completed') {
                color = 'green';
              }
              if (tag === 'Canceled') {
                color = 'red';                
              }
              return (
                  <React.Fragment>
                    <Space size="middle" className="actionfield">
                        {tag === 'Converted' ?                         
                        (<a style={{display: "flex"}}><MdAssignmentTurnedIn className="assignmenticon"/></a>) :
                        <a><img src={BuildCircle} className="settingcircle"/></a> }
                        
                                        
                    </Space>

                <Tag color={color} key={tag} className="tagoption">
                    {tag}
                </Tag>
                </React.Fragment>
              );
            })}
          </>
        ),
      },
    {
    title: 'Date',
    dataIndex: 'date',
    key: 'date',
    },
    {
      title: 'Number',
      dataIndex: 'number',
      key: 'number',
    },
    {
      title: 'Assigned to',
      dataIndex: 'assigned',
      key: 'assigned',
      render: (text, record) => {
        console.log("=====",record)
            return <Link to=""><span className="maincustomername">{record.assigned}</span></Link>
        }
    },
    {
      title: 'Amount',
      dataIndex: 'amount',
      key: 'amount',
    },    
    {
      title: 'Actions',
      dataIndex: 'action',
      key: 'action',
      render: (text, record) => (
        <Space size="middle" className="approvefield">
          <a>View{record.name}</a>
          <MdKeyboardArrowDown className="actionarrow"/>
        </Space>
      ),
    },
  ];
  
  const data = [
    {
      key: '1',
      tags: ['Converted'],
      number: 4562,
      date: '30-8-2020',
      amount: '$550.00',
      action: "Approve  &  send ",
      assigned: 'Peter Jonson',
    //   render: (text, record) => (
    //     <Space size="middle" className="actionfield">
    //         <a><img src={BuildCircle} className="settingcircle"/></a>
    //         <a style={{display: "flex"}}><MdAssignmentTurnedIn className="assignmenticon"/></a>                
    //     </Space>
    // )   
    },
    {
      key: '2',
      tags: ['Completed'],
      number: 4561,
      date:"28-8-2020",
      amount: '$700.00' ,
      action: "Record payment" ,
      assigned: 'Kevin Hart & 1 more'    
    },
    {
      key: '3',
      tags: ['Canceled'],
      number: 4560,
      date: '26-8-2020',
      amount: '$380.00',
      action: "View" ,
      assigned:'Lisa Thunder'   
    },
];


const CustomerHistory = () => { 

    const [focusName, setFocusName] = useState(false);

return (
        <div className="customer_form historysection">
            <div className="customer_section" style={{marginBottom: "20px"}}>
                <div className="finacialdatefield">
                    <div className="container">
                        <div className="valuetable">
                            <ul>
                                <li>
                                    <div>
                                        <p>Lifetime value</p>
                                        <h5>$3570.00</h5>
                                    </div>
                                </li>
                                <li>
                                    <div>
                                        <p>Last month </p>
                                        <h5>$1580.00</h5>
                                    </div>
                                </li>
                                <li className="totaljobs">
                                    <div>
                                        <p>Total jobs</p>
                                        <h5>9</h5>
                                    </div>                                    
                                </li>
                                <li className="avergevalue">
                                    <div>
                                        <p>Average job value</p>
                                        <h5>$548.00</h5>
                                    </div>                                    
                                </li>
                            </ul>
                        </div>
                        <div className="finacicaldatesection">
                            <ul>
                                <li className="workerfield">
                                    <Select defaultValue="All field workers" className="statusfield" onChange={handleChange}>
                                        <Option value="All Status">All field workers</Option>
                                        <Option value="Paid">Paid</Option>
                                        <Option value="Unpaid">Unpaid</Option>
                                    </Select>
                                </li>
                                <li>
                                    <Select defaultValue="All Status" className="statusfield" onChange={handleChange}>
                                        <Option value="All Status">All Status</Option>
                                        <Option value="Paid">Paid</Option>
                                        <Option value="Unpaid">Unpaid</Option>
                                    </Select>
                                </li>
                                <li className="datepickersection">
                                    <Input.Group compact>
                                        <DatePicker style={{ width: '50%' }} />
                                        <DatePicker style={{ width: '50%' }}  />
                                    </Input.Group>
                                </li>
                                <li className="tableserachsection">
                                    <Search
                                    className="searchinvoice"
                                    placeholder="Search job and estimate"
                                    onSearch={value => console.log(value)}
                                    // style={{ width: 320 }}
                                    />
                                </li>
                                <li className="unpaidinvoice">
                                    <span>
                                        <Link>Jobs</Link>
                                    </span>
                                    <span>
                                        <Link>Estimates</Link>
                                    </span>
                                    <span className="invoicebill">
                                        <Link>All tasks</Link>
                                    </span>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
                <div className="container">
                    <div className="customerfinacilatable">
                        <Table 
                        scroll={{ x: 400}}
                        className="customertable" 
                        columns={columns} 
                        dataSource={data} 
                        style={{ whiteSpace: "pre-line" }}
                        />
                    </div>
                    <div className="paginationmainfield">
                        <div>
                            <b className="showfield">Show</b>
                            <Select defaultValue="10" style={{width: "75px"}} onChange={handleChange}>
                                <Option value="10">10</Option>
                                <Option value="25">25</Option>
                                <Option value="50">50</Option>
                            </Select>
                            <b className="perpagefield">per page</b>
                        </div> 
                        <div className="paginationfield">
                            <ul>
                                <li className="ant-pagination-total-text">3-3 <span>of</span> 1</li>
                                <li className="ant-pagination-prev"><MdKeyboardArrowLeft/></li>
                                <li className="ant-pagination-next"><MdKeyboardArrowRight/></li>
                            </ul>
                        </div>                       
                    </div>
                </div>
            </div>
        </div>
        )
}

export default CustomerHistory;