import React from 'react'
import { Card, Input, Col, Row, Select, Checkbox,Collapse  } from 'antd';
import "./CustomerNotes.css";

import { MdArrowBack, MdMoreVert,MdAssignmentTurnedIn } from 'react-icons/md';

import Notes from "../../Assets/images/notes.png";


const { Panel } = Collapse;
const { TextArea } = Input;

const CustomerNotes = () => {
    return (
        <div className="boxfield notesfield">
            <Collapse accordion >
                <span  className="notesicon">
                    <img src={Notes} />
                </span>                
                <Panel header="NOTES  (2)" key="1">
                    Notes                    
                </Panel>
            </Collapse>
        </div>
    )
}

export default CustomerNotes;
