import React, { useState, useEffect } from 'react'
import { Row, Col,Modal, Input, Button, Select } from 'antd';

import "./BusinessAddress.css";


const { Option } = Select;

const BusinessAddress = ({address, visible, handleCancel, confirmChange}) => {
    const [newAddress, setBusinessAddress] = useState({address: "", state: "", city: "", zip: ""});
    // const [value, setBusinessAddressZipCode] = useState('');
    const handleOk = () => {
        confirmChange(newAddress);
    }
    useEffect(() => {
        setBusinessAddress({...address});
    }, [visible])
    const handleAddressChange = (e) => {
        const {name, value} = e.target;
        setBusinessAddress(prevAddress => {
            return {
                ...prevAddress,
                [name]: value
            }
        })
    }
    return (
        <div>
                <Modal
                    className="businessmodel businessaddress"
                    title="Add business address"
                    centered
                    visible={visible}
                    onOk={handleOk}
                    onCancel={handleCancel}
                    footer={null}
                    >
                        <Row gutter={20}>                                
                            <Col xs={24} sm={12} md={12} lg={12} xl={12} className="business_add_content">
                                <div className="businessaddresfield"> 
                                        <div class="field">
                                            <input type="text" name="address" id="fullname" placeholder="Address" value={newAddress.address} onChange={handleAddressChange}/>
                                            <div className="companyfield">
                                                <label for="fullname">Address</label>
                                            </div>                                     
                                        </div>  
                                        <div class="field">
                                            <input type="text" name="city" id="fullname" placeholder="City" value={newAddress.city} onChange={handleAddressChange}/>
                                            <div className="companyfield">
                                                <label for="fullname">City</label>
                                            </div>                                     
                                        </div>
                                        <div class="field">
                                            <input type="text" name="state" id="fullname" placeholder="State" value={newAddress.state} onChange={handleAddressChange}/>
                                            <div className="companyfield">
                                                <label for="fullname">State</label>
                                            </div>                                     
                                        </div>
                                        <div class="field">
                                            <input type="text" name="zip" id="fullname" placeholder="Zip" value={newAddress.zip} onChange={handleAddressChange}/>
                                            <div className="companyfield">
                                                <label for="fullname">Zip</label>
                                            </div>                                     
                                        </div>  
                                        <div class="businessaddressbtn buttonaddress">
                                            <Button key="submit" type="primary" onClick={handleOk}>
                                                Confirm
                                            </Button>
                                            <Button key="back" onClick={handleCancel}>
                                                Cancel
                                            </Button>                                            
                                        </div>                                                                    
                                    {/* <Input value={value} onChange={(e)=>setBusinessAddress(e.target.value)} placeholder="Basic usage" style={{padding: ".7rem 1.4rem"}}/> */}
                                </div>
                            </Col>
                            <Col xs={24} sm={12} md={12} lg={12} xl={12} className="mapfield">
                            <div className="google-map-code">
                                <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d15282225.79979123!2d73.7250245393691!3d20.750301298393563!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x30635ff06b92b791%3A0xd78c4fa1854213a6!2sIndia!5e0!3m2!1sen!2sin!4v1587818542745!5m2!1sen!2sin" width="280" height="243" frameborder="0" style={{border:0}} allowfullscreen="" aria-hidden="false" tabindex="0"></iframe>
                            </div>
                            </Col>
                        </Row>
                        
                        
                </Modal>
        </div>
        
    )
}

export default BusinessAddress;

