import React, { useState, useEffect } from 'react'
import { Row, Col, Tabs, Menu, Dropdown } from 'antd';
import { MdArrowBack, MdMoreVert,MdAssignmentTurnedIn } from 'react-icons/md';
import { Tag } from 'antd';
import {Link} from "react-router-dom";
import CustomerInfo from '../CustomerInfo/CustomerInfo';
import CreateJob from "../../Assets/images/createjob.png";

import "./CustomerProfile.css";
import AdditionalInfo from '../AdditionalInfo/AdditionalInfo';
import CustomerCommunication from '../CustomerCommunication/CustomerCommunication';
import CustomerNotes from '../CustomerNotes/CustomerNotes';
import CustomerAttachments from '../CustomerAttachments/CustomerAttachments';
import CustomerAddress from '../CustomerAddress/CustomerAddress';

import { RiFolderDownloadLine } from 'react-icons/ri';
import { BiExport } from 'react-icons/bi';
import CustomerOverview from '../CustomerOverview/CustomerOverview';
import CustomerCreditCard from '../CustomerCreditCard/CustomerCreditCard';
import CustomerFinacialTable from '../CustomerFinacialTable/CustomerFinacialTable';
import CustomerHistory from '../CustomerHistory/CustomerHistory';
import CustomerSchedule from '../CustomerSchedule/CustomerSchedule';
import CustomerUpCommingTask from '../CustomerUpCommingTask/CustomerUpCommingTask';
import CreateCustomeField from '../Modal/CreateCustomeField/CreateCustomeField';





const { TabPane } = Tabs;

function callback(key) {
    console.log(key);
  }


  
  

const CustomerProfile = () => {

    
   const [fixtab, setFixtab] = useState(false);

   const addclass = () => {
        if(window.scrollY >= 70) {
            setFixtab(true);
        }else {
            setFixtab(false);
        }
   }

   window.addEventListener('scroll', addclass);


   const [visible, setVisible] = useState(false);


   const handleShowModal = () => {
        setVisible(true);
        
   }

   const handleCloseModal = () => {
        setVisible(false);
   }

    const menu = (
        <Menu className="viewmenu">
          <Menu.Item key="0">
            <Link to=""> <RiFolderDownloadLine/>Import</Link>
          </Menu.Item>
          <Menu.Item key="1">
            <Link to=""> <BiExport/>Export</Link>
          </Menu.Item>
        </Menu>
      );

    return (
        <div className="customer_form">
        <div className="customer_section" style={{marginBottom: "20px"}}>
            <div className="maincustomerfield customerprofiel_section">
                    <div className="customerprofile">
                            <div className="customerprofile_left">
                                <div className="backbtn">
                                    <Link to="/customers">
                                        <MdArrowBack/>
                                        Back
                                    </Link>                                    
                                </div>
                                <div className="customerheading">
                                    <h4>Peter Windberg</h4>
                                    <Tag>Active</Tag>
                                </div>
                            </div>
                            <div className="customerprofile_right">
                                <div className="btnsection">
                                    <div className="addcustomerbtn">                                  
                                        <Link 
                                           onClick={handleShowModal}
                                        >
                                            <img src={CreateJob} />
                                            CREATE JOB
                                        </Link>
                                    </div>
                                    <div className="addcustomerbtn">                                  
                                        <Link to="">
                                            <MdAssignmentTurnedIn/>
                                            CREATE ESTIMATE
                                        </Link>
                                    </div>
                                </div>
                                <div className="morebtnfield">
                                    <Dropdown overlay={menu} trigger={['click']}>
                                        <a onClick={e => e.preventDefault()}><MdMoreVert/></a>                                       
                                    </Dropdown>
                                </div>                                
                            </div>
                    </div>
                    <div className={fixtab ? 'customerprofile_tab stickytab' : 'customerprofile_tab'} >
                        <Tabs defaultActiveKey="1" onChange={callback}>
                            <TabPane tab="Profile" key="1">
                                <Row gutter={30}>
                                    <Col xs={24} sm={24} md={12} lg={8} xl={8} className="companyprofile_left">
                                            <CustomerInfo/>
                                            <AdditionalInfo/>
                                            <CustomerCommunication/>
                                            <CustomerNotes/>
                                            <CustomerAttachments/>
                                    </Col>
                                    <Col xs={24} sm={24} md={12} lg={16} xl={16} className="companyprofile_right">
                                       <CustomerAddress/>
                                    </Col>                    
                                </Row>
                            </TabPane>

                            <TabPane tab="Financial" key="2">
                                <Row gutter={30}>
                                    <Col xs={24} sm={24} md={24} lg={16} xl={16} className="companyprofile_right">
                                       <CustomerFinacialTable/>
                                    </Col>
                                    <Col xs={24} sm={24} md={24} lg={8} xl={8} className="companyprofile_left">
                                        <div className="maincrditright">
                                            <CustomerOverview/>
                                            <CustomerCreditCard/>
                                        </div>         
                                    </Col>                                                        
                                </Row>
                            </TabPane>
                            <TabPane tab="Scheduled jobs & estimates" key="3">
                                <Row gutter={30}>
                                    <Col xs={24} sm={24} md={24} lg={16} xl={16} className="companyprofile_right">
                                        <CustomerSchedule/>
                                    </Col>
                                    <Col xs={24} sm={24} md={24} lg={8} xl={8} className="companyprofile_left">                                        
                                        <CustomerUpCommingTask/>
                                    </Col>                                                        
                                </Row>
                            </TabPane>
                            <TabPane tab="History" key="4">
                                <CustomerHistory/>
                            </TabPane>
                            
                            
                        </Tabs>
                    </div>
            </div>
            <div className="container">
               
               
            </div>
        </div>
        <CreateCustomeField visible={visible} handleCancel={handleCloseModal}/>
    </div>
    
    )
}

export default CustomerProfile;

