import React, { useState, useEffect } from 'react'
import { Row, Col,Modal, Input, Button, Select } from 'antd';

// import "./BusinessServiceType.css";

const { Option } = Select;

const options = [
    {
      value: 'zhejiang',
      label: 'Zhejiang',
      children: [
        {
          value: 'hangzhou',
          label: 'Hangzhou',
          children: [
            {
              value: 'xihu',
              label: 'West Lake',
            },
          ],
        },
      ],
    },
    {
      value: 'jiangsu',
      label: 'Jiangsu',
      children: [
        {
          value: 'nanjing',
          label: 'Nanjing',
          children: [
            {
              value: 'zhonghuamen',
              label: 'Zhong Hua Men',
            },
          ],
        },
      ],
    },
  ];

const TimeFormat = ({busines, visible, handleCancel, confirmChange}) => {
    const [value, settimeformat] = useState('');
    const handleOk = () => {
        confirmChange(value);
    }
    useEffect(() => {
        settimeformat(busines)
    }, [visible])
    return (
        <div>
                <Modal
                    className="businessmodel responsivebtn"
                    title="Change time format"
                    centered
                    visible={visible}
                    onOk={handleOk}
                    onCancel={handleCancel}
                    footer={null}
                    // footer={[
                    //     <Button key="back" onClick={handleCancel}>
                    //       Cancel
                    //     </Button>,
                    //     <Button key="submit" type="primary" onClick={handleOk}>
                    //       Confirm
                    //     </Button>,
                    // ]}
                    >
                        <Select defaultValue="3:35 PM" onChange={(selectedOption)=>settimeformat(selectedOption)} style={{ width: '100%' }} >
                            <Option value="3:35 PM">3:35 PM</Option>
                            <Option value="3:35 pm">3:35 pm</Option>
                            <Option value="15:35">15:35</Option>
                        </Select> 
                        {/* <Input  placeholder="Basic usage" style={{padding: ".7rem 1.4rem"}}/> */}
                        <div class="businessaddressbtn">
                            <Button key="submit" type="primary" onClick={handleOk}>
                                Confirm
                            </Button>
                            <Button key="back" onClick={handleCancel}>
                                Cancel
                            </Button>                                            
                        </div> 
                </Modal>
        </div>
        
    )
}

export default TimeFormat;

