import React, {useState} from 'react';
import { Card, Input, Col, Row, Select, Checkbox, Form, Space, Button } from 'antd';
import { MinusCircleOutlined, PlusOutlined } from '@ant-design/icons';
import "./CustomerDetails.css";
import Stargold from "../../Assets/images/stargold.svg";
import Deletbtn from "../../Assets/images/deletbtn.png";
import Whitestar from "../../Assets/images/whitestar.svg";
// import { StarFilled } from '@ant-design/icons';
import { GiRoundStar } from "react-icons/gi";
import { RiDeleteBin2Line } from "react-icons/ri";
import { HiOutlineStar } from "react-icons/hi";
import { MdDeleteForever } from "react-icons/md";











const { Option } = Select;

function onChange(e) {
    console.log(`checked = ${e.target.checked}`);
}

const options = [
    {
      value: 'zhejiang',
      label: 'Zhejiang',
      children: [
        {
          value: 'hangzhou',
          label: 'Hangzhou',
          children: [
            {
              value: 'xihu',
              label: 'West Lake',
            },
          ],
        },
      ],
    },
    {
      value: 'jiangsu',
      label: 'Jiangsu',
      children: [
        {
          value: 'nanjing',
          label: 'Nanjing',
          children: [
            {
              value: 'zhonghuamen',
              label: 'Zhong Hua Men',
            },
          ],
        },
      ],
    },
  ];

const CustomerDetails = () => {
    const onFinish = values => {
        console.log('Received values of form:', values);
      };

      const [focusName, setFocusName] = useState(false);
      
    return (
        <div className="boxfield">
            <Card title="Customer details">
                        <Row gutter={12}>
                                <Col xs={24} sm={8} md={8} lg={8} xl={8} className="mobilemar12">
                                    <Select defaultValue="No title" style={{ marginRight: '1.2rem' }}>
                                        <Option value="No title">No title</Option>
                                        <Option value="Mr.">Mr.</Option>
                                        <Option value="Ms.">Ms.</Option>
                                        <Option value="Mrs.">Mrs.</Option>
                                        <Option value="Miss.">Miss.</Option>
                                        <Option value="Dr.">Dr.</Option>
                                    </Select> 
                                </Col>
                                <Col xs={24} sm={16} md={16} lg={16} xl={16}>
                                    <Row gutter={0} className="inputfieldbox" >
                                        <Col xs={24} sm={12} md={12} lg={12} xl={12} className="mobilemar12">
                                            <div class="field">
                                                <input type="text" onFocus={()=>setFocusName(true)} onBlur={()=>setFocusName(false)} name="Firstname" id="Firstname" placeholder="First name" className="borderrightnone" />
                                                <div className="companyfield">
                                                    <label for="Firstname">First name</label>
                                                </div>                                     
                                            </div>                                            
                                        </Col>
                                        <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                                            <div class="field">
                                                <input type="text" name="Lastname" id="Lastname" placeholder="Last name" className={focusName ? 'borderleftnone onfocusbordernone' : 'borderleftnone'} />
                                                <div className="companyfield">
                                                    <label for="Lastname">Last name</label>
                                                </div>                                     
                                            </div>
                                        </Col>
                                    </Row>  
                                   
                                </Col>
                                <Col span={24} style={{paddingTop: "12px"}}>
                                    <div class="field">
                                        <input type="text" name="fullname" id="fullname" placeholder="Company name"/>
                                        <div className="companyfield">
                                            <label for="fullname">Company name</label>
                                        </div>                                     
                                    </div>
                                </Col>
                                <Col span={24} style={{paddingTop: "17px",textAlign: "left"}} className="checkboxfield">
                                    <Checkbox onChange={onChange}>Use company name as the primary name</Checkbox>
                                </Col>
                                <Col span={24} style={{paddingTop: "26px",textAlign: "left"}} className="">
                                    <h4 className="boxsubheading">Contact details</h4>
                                </Col>
                                <Col span={8} style={{paddingTop: "14px"}}>
                                    <Select defaultValue="Home" style={{ marginRight: '1.2rem' }}>
                                        <Option value="Main">Main</Option>
                                        <Option value="Home">Home</Option>
                                        <Option value="Work">Work</Option>
                                        <Option value="Fax">Fax</Option>
                                        <Option value="Other">Other</Option>
                                    </Select> 
                                </Col>                                
                                <Col span={16} style={{paddingTop: "14px"}}>                                  
                                    <div class="field">
                                        <input type="text" name="phonenumber" id="phonenumber2" placeholder="Phone number"/>
                                        <div className="companyfield">
                                            <label for="phonenumber2">Phone number</label>
                                        </div>                                     
                                    </div>
                                </Col>
                                {/* <Col span={24} style={{paddingTop: "16px",textAlign: "left"}} className="">
                                    <h5 className="inputbottomheading">ADD PHONE NUMBER</h5>
                                </Col> */}
                                <Col span={24} style={{paddingTop: "16px",textAlign: "left"}} className="">
                                    <Form
                                    // initialValues={{ users: [{fieldKey: 0, isListField: true, key: 0, name: 0}] }}
                                     name="dynamic_form_nest_item" onFinish={onFinish} autoComplete="off">                                        
                                    <Form.List name="users">
                                        {(fields, { add, remove }) => {
                                        return (
                                            <div className="contactadd">
                                                {fields.map((fieldsection, index) => (
                                                    <Space key={fieldsection.key} style={{ display: 'flex', marginBottom: 14 }} align="start" className="maincontact">
                                                        {
                                                            index === 0 ? (
                                                                // <GiRoundStar style={{color: '#f3b40c', fontSize: '18px'}} />
                                                                <img src={Stargold} />
                                                            ): (
                                                                // <HiOutlineStar style={{color: '#c3c3c3', fontSize: '19px'}} className="deletebtn"/>
                                                                <img src={Whitestar} className="deletebtn"/>
                                                            )
                                                        }
                                                            
                                                            <Select defaultValue="Home" style={{ marginRight: '1.2rem' }}>
                                                                <Option value="Main">Main</Option>
                                                                <Option value="Home">Home</Option>
                                                                <Option value="Work">Work</Option>
                                                                <Option value="Fax">Fax</Option>
                                                                <Option value="Other">Other</Option>
                                                            </Select> 
                                                            <div class="field">
                                                                <input type="text" name="phonenumber" id="phonenumber2" placeholder="Phone number"/>
                                                                <div className="companyfield">
                                                                    <label for="phonenumber2">Phone number</label>
                                                                </div>                                     
                                                            </div>
                                                            <MdDeleteForever
                                                                style={{color: '#f55555', fontSize: '24px', cursor: "pointer"}}
                                                                onClick={() => {
                                                                remove(fieldsection.name);
                                                                }}
                                                            />                                                       
                                                    </Space>
                                                ))}
                                                    <Form.Item>
                                                        <h5 
                                                        type="dashed"
                                                        onClick={() => {
                                                            add();
                                                        }}
                                                        block className="inputbottomheading">ADD PHONE NUMBER</h5>
                                                    </Form.Item>
                                            </div>
                                        );
                                        }}
                                    </Form.List>
                                    </Form>
                                </Col>
                                <Col span={8} style={{paddingTop: "28px"}}>
                                    <Select defaultValue="Work" style={{ marginRight: '1.2rem' }}>
                                        <Option value="Work">Work</Option>
                                        <Option value="Personal">Personal</Option>
                                        <Option value="Other">Other</Option>
                                    </Select> 
                                </Col>
                                <Col span={16} style={{paddingTop: "28px"}}>
                                    <div className="inputfieldbox">
                                        <div class="field">
                                            <input type="text" name="Emailaddress" id="Emailaddress" placeholder="Email address"/>
                                            <div className="companyfield">
                                                <label for="Emailaddress">Email address</label>
                                            </div>                                     
                                        </div>
                                    </div>                                    
                                </Col>
                                {/* <Col span={24} style={{paddingTop: "16px",textAlign: "left"}} className="">
                                    <h5 className="inputbottomheading">ADD EMAIL ADDRESS</h5>
                                </Col> */}

                                <Col span={24} style={{paddingTop: "16px",textAlign: "left"}} className="">
                                    <Form
                                    // initialValues={{ users: [{fieldKey: 0, isListField: true, key: 0, name: 0}] }}
                                     name="dynamic_form_nest_item" onFinish={onFinish} autoComplete="off">                                        
                                    <Form.List name="users">
                                        {(fields, { add, remove }) => {
                                        return (
                                            <div className="contactadd">
                                                {fields.map((fieldsection, index) => (
                                                    <Space key={fieldsection.key} style={{ display: 'flex', marginBottom: 14 }} align="start" className="maincontact">
                                                        {
                                                            index === 0 ? (
                                                                // <GiRoundStar style={{color: '#f3b40c', fontSize: '18px'}} />
                                                                <img src={Stargold} />
                                                            ): (
                                                                // <HiOutlineStar style={{color: '#c3c3c3', fontSize: '19px'}} className="deletebtn"/>
                                                                <img src={Whitestar} className="deletebtn"/>
                                                            )
                                                        }
                                                            
                                                            <Select defaultValue="Work" style={{ marginRight: '1.2rem' }}>
                                                                <Option value="Work">Work</Option>
                                                                <Option value="Personal">Personal</Option>
                                                                <Option value="Other">Other</Option>
                                                            </Select> 
                                                            
                                                            <div className="inputfieldbox">
                                                                <div class="field">
                                                                    <input type="text" name="Emailaddress" id="Emailaddress2" placeholder="Email address"/>
                                                                    <div className="companyfield">
                                                                        <label for="Emailaddress2">Email address</label>
                                                                    </div>                                     
                                                                </div>
                                                            </div> 
                                                            <MdDeleteForever
                                                                style={{color: '#f55555', fontSize: '24px', cursor: "pointer"}}
                                                                onClick={() => {
                                                                remove(fieldsection.name);
                                                                }}
                                                            />                                                       
                                                    </Space>
                                                ))}
                                                    <Form.Item>
                                                        <h5 
                                                        type="dashed"
                                                        onClick={() => {
                                                            add();
                                                        }}
                                                        block className="inputbottomheading">ADD PHONE NUMBER</h5>
                                                    </Form.Item>
                                            </div>
                                        );
                                        }}
                                    </Form.List>
                                    </Form>
                                </Col>

                        </Row>   
            </Card>
        </div>
    )
}

export default CustomerDetails;
