import React from 'react'
import { Card, Input, Col, Row, Select, Checkbox, Menu, Dropdown   } from 'antd';
import "./CustomerAddress.css";

import { GoLocation } from "react-icons/go";
import { Link } from 'react-router-dom';
import { MdAdd, MdMoreVert } from 'react-icons/md';


import Vectorpaint from "../../Assets/images/vectorpaint.svg";
import Key from "../../Assets/images/key.svg";

import { RiFolderDownloadLine } from 'react-icons/ri';
import { BiExport } from 'react-icons/bi';


const { Search } = Input;

const menu = (
    <Menu className="viewmenu">
      <Menu.Item key="0">
        <Link to=""> <RiFolderDownloadLine/>Import</Link>
      </Menu.Item>
      <Menu.Item key="1">
        <Link to=""> <BiExport/>Export</Link>
      </Menu.Item>
    </Menu>
  );

const CustomerAddress = () => {
    return (
        <div className="boxfield customeraddressfield">
            <div>
                <div className="google-map-code">
                    <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d15282225.79979123!2d73.7250245393691!3d20.750301298393563!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x30635ff06b92b791%3A0xd78c4fa1854213a6!2sIndia!5e0!3m2!1sen!2sin!4v1587818542745!5m2!1sen!2sin" width="280" height="204" frameborder="0" style={{border:0}} allowfullscreen="" aria-hidden="false" tabindex="0"></iframe>
                </div>
                <Row gutter={10} className="address_section">                    
                    <Col xs={24} sm={24} md={24} lg={8} xl={8} className="map_section">
                        <div className="mapaddress_heading">
                            <GoLocation/>
                            <h4>ADDRESS  (1)</h4>
                        </div>                        
                    </Col>
                    <Col xs={24} sm={24} md={24} lg={16} xl={16}>
                         <div className="searchsection">
                                <Search
                                placeholder="search"
                                // value="search"
                                onSearch={value => console.log(value)}
                                />
                                <Link to="" className="add_new_button">
                                    <MdAdd/>
                                    Add new
                                </Link>
                         </div>
                    </Col>                    
                </Row>

                <div className="address_section_detail"> 
                        <div className="left_address_field">
                            <div className="mainaddress_field">
                                <div className="customer_section_address">
                                    <p>
                                        2655 Speers Road <br/>
                                        Oakville, ON L6J 3X4
                                    </p>
                                </div>
                                <div className="billing_section">
                                    <Link to="">
                                        Billing
                                    </Link>
                                </div>
                            </div>
                            <ul className="address_notification">
                                <li>
                                    <img src={Vectorpaint} />
                                </li>
                                <li>
                                    <img src={Key}/>
                                </li>
                            </ul>
                        </div>   
                        <div className="right_address_field">
                                <Dropdown overlay={menu} trigger={['click']}>
                                        <a onClick={e => e.preventDefault()}><MdMoreVert/></a>                                       
                                </Dropdown>
                        </div>
                    </div> 
            </div>

        </div>
    )
}

export default CustomerAddress;
